@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
*,*::before,*::after{box-sizing:border-box}body{font-size:65.5%;margin:0;font-family:"Roboto";height:100vh;background:linear-gradient(180deg, #deff5a 0%, #08a7b3 100%) no-repeat;position:relative;z-index:1;overflow:hidden}header{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:flex-start;align-items:flex-start;padding:.5rem;min-height:10vh}header div{color:#08a9b4;position:absolute;right:10px;top:0;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin-right:.5rem;cursor:pointer}header div:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}header div>*{margin:0}.menu{display:-webkit-flex;display:flex;z-index:2;width:100%;margin:0 auto;-webkit-justify-content:center;justify-content:center;background:#06a9a0;min-height:4rem;-webkit-align-items:flex-end;align-items:flex-end}.menu>*{margin-bottom:1rem;margin-left:8%}.menu a{text-decoration:none;color:#fff}.your-club-parent{position:relative;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.your-club-parent img{width:40%;position:absolute;bottom:1;margin-top:15%}.your-club-child{list-style:none;color:#deff5c;width:300%;position:absolute;margin-top:60%;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:flex-end;align-items:flex-end;z-index:10}.your-club-child a{display:-webkit-flex;display:flex;margin-top:.1rem;width:100%;border:1px solid #deff5c;padding:.75rem;background:#09a9a0;-webkit-justify-content:center;justify-content:center;color:#deff5a}.your-club-child a:hover{background:#deff5c;color:#08a9b4;cursor:pointer}#home{background:rgba(229,229,229,.25);text-align:center;min-height:80vh}#home h1{font-size:48px;color:#08a9b4;margin-top:2rem}#soccer{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;width:100%;-webkit-align-items:center;align-items:center;min-height:80vh;background:rgba(229,229,229,.25)}#soccer h1{font-size:48px;color:#08a9b4}#soccer .soccer-input{overflow-x:hidden;border-radius:10px;overflow-y:scroll;text-align:center;max-height:40vh;position:relative;width:40vw;border-radius:15px}#soccer .soccer-input img{background:#deff5c;border-radius:25px}#soccer .soccer-input h3{color:#deff5c;border-bottom:1px solid #deff5c;padding:1rem 0;cursor:pointer}#soccer .soccer-input h3:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}#soccer .soccer-input .inner{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;position:-webkit-sticky;position:sticky;top:0;background:#08a9b4;border:4px solid #deff5c;border-radius:25px}#soccer .soccer-input .inner input{border:none;background:transparent;color:#deff5c}#soccer .soccer-input .inner input::-webkit-input-placeholder{color:#deff5c}#soccer .soccer-input .inner input:-ms-input-placeholder{color:#deff5c}#soccer .soccer-input .inner input::placeholder{color:#deff5c}#soccer ::-webkit-scrollbar{display:none}
